import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/blur-calcium-close-up-dairy-236010.jpg"

const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Las mejores fuentes de calcio para las personas con diabetes"
                meta="Tener diabetes te pone en un mayor riesgo de osteoporosis. Aliméntese con estos alimentos ricos en calcio que le ayudarán a fortalecer sus huesos sin desestabilizar su azúcar en la sangre."
                description="Tener diabetes te pone en un mayor riesgo de osteoporosis. Aliméntese con estos alimentos ricos en calcio que le ayudarán a fortalecer sus huesos sin desestabilizar su azúcar en la sangre."
                lang="es"
            />
            <h1>Las mejores fuentes de calcio para las personas con diabetes</h1>
            <hr/>
            <p class="introductionText">Tener diabetes te pone en un mayor riesgo de osteoporosis. Aliméntese con estos alimentos ricos en calcio que le ayudarán a fortalecer sus huesos sin desestabilizar su azúcar en la sangre.</p>
            <hr/>
            <p></p>
            <div>
                <h2>Por qu&eacute; los alimentos con calcio deben formar parte de su dieta para la diabetes</h2>

                <p>Cuando se tiene diabetes, es importante modificar la dieta para asegurar que el az&uacute;car en la
                    sangre y el peso se mantengan controlados. Eso significa que hay que alimentarte con alimentos como
                    prote&iacute;nas magras, granos enteros, verduras sin almid&oacute;n y ciertas frutas. Pero hay otro
                    tipo de comida beneficiosa que quiz&aacute; no haya considerado a&ntilde;adir a su dieta para la
                    diabetes: los alimentos ricos en calcio. <b>Las investigaciones sugieren que el mineral no s&oacute;lo
                    puede ayudar a contrarrestar el riesgo de osteoporosis, con el que est&aacute; asociada la diabetes,
                    sino que tambi&eacute;n puede ayudar a detener el avance de la enfermedad en aquellos a los que se
                        les ha diagnosticado.</b></p>
                <p></p>
                <p>&quot;La suplementaci&oacute;n tanto de calcio como de vitamina D ha demostrado ser beneficiosa para
                    optimizar el metabolismo de la glucosa&quot;, dice Melissa Joy Dobbins, RDN, CDE, quien tiene su
                    base en Chicago. Esto es crucial cuando se tiene diabetes, que se caracteriza por la resistencia a
                    la insulina, una condici&oacute;n que impide que la glucosa llegue a las c&eacute;lulas para obtener
                    energ&iacute;a o que se almacene para su uso posterior, lo que da lugar a una acumulaci&oacute;n de
                    esa glucosa en el torrente sangu&iacute;neo. Un estudio publicado en octubre de 2014 en PLOS One
                    respalda esta idea, sugiriendo que la suplementaci&oacute;n diaria con calcio y vitamina D durante
                    un per&iacute;odo de seis meses puede ayudar a mejorar la sensibilidad a la insulina en las personas
                    con prediabetes.</p>
                <p></p>
                <div style={{textAlign:`center`,margin:'50px'}}>
                    <img src={foto1} style={{width:`50vh`}} alt="vaso de leche"></img>
                </div>
                <p>Los beneficios potenciales de los alimentos ricos en calcio para la diabetes no terminan ah&iacute;.
                    El mineral tambi&eacute;n puede beneficiar a las personas con prediabetes, el precursor de la forma
                    completa de la enfermedad, as&iacute; como a las personas que corren el riesgo de padecer
                    prediabetes en funci&oacute;n de sus antecedentes familiares, su dieta y su estilo de vida, o su
                    origen &eacute;tnico.</p>
                <p></p>
                <p>&quot;Estudios de observaci&oacute;n han demostrado una asociaci&oacute;n entre el bajo nivel de
                    vitamina D, la ingesta de calcio y la prevalencia de la diabetes tipo 2 y el s&iacute;ndrome
                    metab&oacute;lico&quot;, dice Angela Ginn-Meadow, RD, CDE, coordinadora de educaci&oacute;n superior
                    del Centro de Diabetes y Endocrinolog&iacute;a de la Universidad de Maryland y propietaria de la
                    empresa consultora Real Talk Real Food en Baltimore.</p>
                <p></p>
                <p>Los beneficios del calcio en la salud &oacute;sea de las personas con diabetes tampoco deben pasarse
                    por alto. Tener diabetes tipo 1 o tipo 2 se ha relacionado con un mayor riesgo de osteoporosis, una
                    condici&oacute;n caracterizada por una baja densidad &oacute;sea, as&iacute; como
                    fracturas &oacute;seas. Una revisi&oacute;n publicada en julio de 2011 en Diabetes/Metabolism
                    Research and Reviews encontr&oacute; que las personas con diabetes ten&iacute;an m&aacute;s
                    probabilidades de sufrir fracturas de cadera, pie y columna vertebral que los individuos sin
                    diabetes. Este aumento del riesgo es una raz&oacute;n m&aacute;s para asegurarse de que se
                    est&aacute; cargando con alimentos ricos en calcio cuando se tiene diabetes.</p>
                <p></p>
                <p>Entonces, &iquest;cu&aacute;nto calcio necesita? Para las mujeres y los hombres de entre 19 y 51
                    a&ntilde;os, la cantidad diaria recomendada de calcio es de 1.000 miligramos (mg) por d&iacute;a.
                    Esta recomendaci&oacute;n aumenta a 1.200 mg para las mujeres de 51 a&ntilde;os o m&aacute;s y los
                    hombres de 71 a&ntilde;os o m&aacute;s, seg&uacute;n los Institutos Nacionales de Salud (NIH).</p>
                <p></p>
                <p>No obstante, ciertos alimentos ricos en calcio son mejores que otros para las personas con diabetes.</p>
                <p><b>&quot;Las sardinas apoyan la salud cardiovascular, y la prote&iacute;na puede ayudar con la
                    sensibilidad a la insulina&quot;, explica Ginn-Meadow. Con 324 mg de calcio por cada porci&oacute;n
                    de 3 onzas, este pez proporciona una excelente fuente de calcio. Adem&aacute;s, la vitamina D de las
                    sardinas puede ayudar al cuerpo a utilizar el calcio de manera m&aacute;s eficiente y a protegerse
                    contra las deficiencias de la vitamina liposoluble, as&iacute; como a aumentar la ingesta
                    diet&eacute;tica.</b></p>
                <p></p>
                <p>Pero el mayor beneficio de incorporar las sardinas en la dieta de la diabetes puede provenir de
                    los &aacute;cidos grasos omega-3 de origen marino que este pescado puede proporcionar.</p>
                <p></p>
                <p>Los &aacute;cidos grasos de las sardinas pueden ofrecer una capa adicional de protecci&oacute;n
                    contra las enfermedades card&iacute;acas. Eso puede ser una ventaja cr&iacute;tica para las personas
                    con diabetes. Seg&uacute;n la Asociaci&oacute;n Americana del Coraz&oacute;n, al menos el 68 por
                    ciento de las personas con diabetes mayores de 65 a&ntilde;os morir&aacute;n de alguna forma de
                    enfermedad card&iacute;aca, y los Centros para el Control y la Prevenci&oacute;n de Enfermedades
                    estiman que las personas con diabetes tienen el doble de probabilidades de morir de una enfermedad
                    card&iacute;aca que las personas sin diabetes.</p>
                <p></p>
                <p>Sin embargo, aumentar la ingesta de &aacute;cidos grasos omega-3 de origen marino, como los que se
                    encuentran en las sardinas, puede ayudar a protegerse contra esta complicaci&oacute;n de la
                    diabetes. Un meta-an&aacute;lisis publicado en enero de 2017 en Mayo Clinic Proceedings
                    encontr&oacute; que el &aacute;cido eicosapentaenoico y el &aacute;cido docosahexaenoico - dos tipos
                    de grasas omega-3 que se encuentran com&uacute;nmente en los mariscos - pueden ayudar a reducir el
                    riesgo de enfermedades coronarias, especialmente en poblaciones de alto riesgo, como las personas
                    con altos niveles de triglic&eacute;ridos y colesterol LDL (&quot;malo&quot;).&nbsp;</p>
                <p></p>
            </div>

        </Layout>
    )
}

export default Ubuntu
